<template>
    <div class="overlay-content" style="border: solid">
        <!-- <p @click="click('닫기')">닫기</p> -->
        <div><p>{{data.row.title}}</p></div>
        <div v-for="(item, index) in data.rows" v-bind:key="index">
            <!-- <a :href="item.url">title</a> -->
            <p @click="click('선택', item)">{{item.title}}</p>
        </div>
        <p style="background: red" @click="click('Post')">Post</p>
    </div>
</template>
<style scoped>
a{
  cursor:pointer;
}
.overlay-content {
  position: relative;
  top: 25%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

.overlay a:hover, .overlay a:focus {
  color: #f1f1f1;
}

.overlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 60px;
}
</style>

<script>

export default {      
  name: 'SampleDirectHandler',
  props: {
    data: Object,
  },
  mounted() {
    // todo : 로딩 직후 처리할 작업
    console.log('mounted>', this.data);
  },
  computed: {
  },
  filters: {
  },
  methods: {
      click(type, item){
          switch (type) {
              case 'Post':                  
                {
                  // direct
                  const directType = 'template';
                  const params = {
                    seq: this.data.rows.length,
                  };                    

                  this.$store.dispatch('POST', {                      
                        directType,
                        params,
                    },
                  ).then((res) => {
                    console.log('result', res);
                    const {result} = res;
                    const {list} = result;
                    this.data.rows = this.data.rows.concat(list);
                  });
                }
                break;          
              default:
                alert(item.title);
                break;
          }
      },
      close(){
          this.$uiHandler.closePopup();
      },
  },
};
</script>